import styled from "styled-components";
import logoImage from "../assets/images/logo.png";
import Lineage from "../assets/images/games/Untitled-2-01.png";
import Mue from "../assets/images/games/Untitled-2-02.png"
import CountryOfWind from "../assets/images/games/Untitled-2-03.png"
import Maple from "../assets/images/games/Untitled-2-04.png"
import Diablo from "../assets/images/games/Untitled-2-05.png"
import Iron from "../assets/images/games/Untitled-2-06.png"
import DarkYeden from "../assets/images/games/Untitled-2-07.png"
import Dunpa from "../assets/images/games/Untitled-2-08.png"
import StonAge from "../assets/images/games/Untitled-2-09.png"
import Etc from "../assets/images/games/Untitled-2-10.png"
import CS from "../assets/images/games/Untitled-2-11.png"
import {useNavigate} from "react-router-dom";

const Header = () => {
    const navigate = useNavigate();

    const navigationMain = () => {
        navigate("/");
    };

    const MenuValues = [
        {name: "", image: Lineage, hoverOptions: {option1: "2", option2: "", option3: "M"}},
        {name: "", image: Mue, hoverOptions: {}},
        {name: "", image: CountryOfWind, hoverOptions: {}},
        {name: "", image: Maple, hoverOptions: {}},
        {name: "", image: Diablo, hoverOptions: {}},
        {name: "", image: Iron, hoverOptions: {}},
        {name: "", image: DarkYeden, hoverOptions: {}},
        {name: "", image: Dunpa, hoverOptions: {}},
        {name: "", image: StonAge, hoverOptions: {}},
        {name: "", image: Etc, hoverOptions: {}},
        {name: "", image: CS, hoverOptions: {}},
    ];

    return (
        <MainHeader>
            <LogoImage onClick={navigationMain}/>
            {MenuValues.map((game) => (
                <GameMenu key={game.name}>
                    <GameImage style={{backgroundImage: `url(${game.image})`}}/>
                    <GameName>{game.name}</GameName>
                    {Object.keys(game.hoverOptions).length > 0 && (
                        <DropdownContent className="dropdown-content">
                            {Object.entries(game.hoverOptions).map(([key, value], index, array) => (
                                <DropdownItem key={key}>{value}</DropdownItem>
                            ))}
                        </DropdownContent>
                    )}
                </GameMenu>
            ))}
        </MainHeader>
    );
}

const MainHeader = styled.header`
    width: 100%;
    height: 70px;
    margin: 0 auto;
    display: flex;
    color: white;
    background-color: black;
`;

const LogoImage = styled.div`
    background-image: url(${logoImage});
    width: 200px;
    height: 100%;
    padding-right: 10px;
    background-size: contain;
    background-repeat: no-repeat;
    cursor: pointer;
`;

//      ,
const GameMenu = styled.div`
    //      
    flex: 0 1 auto;
    padding: 0 10px; //    
    display: flex;
    align-items: center; //   
    justify-content: center; //   
    cursor: pointer;
    position: relative; //    relative 
    &:hover {
        background-color: white; //   
        color: black;
        font-weight: bold;
        > div.dropdown-content {
            display: block; //  
            background-color: black;
        }
    }
`;

const GameImage = styled.div`
    width: 40px; //   
    height: 40px; //   
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white; //    
    margin: 0 5px; //     
`;

const DropdownContent = styled.div`
    display: none; //  
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1; //    
    left: 0; // GameMenu  
    top: 100%; // GameMenu  
`;

const DropdownItem = styled.div`
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
    cursor: pointer;
    //   
    border-bottom: gray;
`;

const GameName = styled.div`
    font-weight: bold;
`;

export default Header;